.card{
    cursor: pointer;
    position: relative;
    width: 360px;
    height: 360px;
    transition: 0.3s ease-out;
    text-align: center;
    overflow: hidden;
}

.card .imgBox{
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.card .imgBox img{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: block;
    transition: 0.5s;
}

.card .content{
    position: absolute;
    left: 0;
    bottom: 0;
    padding: 6px;
    width: 100%;
    height: 80px;
    background: #00d0ff;
    transition: all 0.5s cubic-bezier(.48,-0.28,0.41,1.4);
    box-sizing: border-box;
    overflow: hidden;
}

.card:hover .content{
    width: 100%;
    height: 80%;
    left: 0;
    bottom: 0;
}

.content p{
    margin: 10px 0 0;
    transform: translateY(2em);
    line-height: 1.3em;
    transition: justify;
    opacity: 0;
}
.card:hover .content p{
    transform: translateY(0);
    opacity: 1;
}
.card:hover{
    transform: scale(1.08);
}
@media only screen and (max-width: 500px) {
