$sub-color: grey;
$main-color: black;

@mixin shrinkLabel {
	top: -14px;
	font-size: 12px;
	color: $main-color;
}

.group {
	position: relative;
	margin: 45px 0;

	.form-input {
		background: none;
		background-color: white;
		color: $main-color;
		font-size: 18px;
		padding: 10px 10px 10px 5px;
		display: block;
		width: 100%;
		border: none;
		border-radius: 0;
		border-bottom: 1px solid $sub-color;
		margin: 25px 0;

		&:focus {
			outline: none;
		}

		&:focus ~ .form-input-label {
			@include shrinkLabel();
		}
	}
}

.form-input-label {
	color: $sub-color;
	font-size: 16px;
	font-weight: normal;
	position: absolute;
	pointer-events: none;
	left: 5px;
	top: 10px;
	transition: 300ms ease all;

	&.shrink {
		@include shrinkLabel();
	}
}

.register
{
	width: 30vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	margin: 50px;
}